import { Component, OnInit } from '@angular/core';
import { BusinessService } from "./../../core/business.service";
import { Router } from '@angular/router';
import { Meta, Title } from "@angular/platform-browser";

@Component({
  selector: 'app-apropos',
  templateUrl: './apropos.component.html',
  styleUrls: ['./apropos.component.css']
})
export class AproposComponent implements OnInit {
  Business
  today = new Date();
  romdhane=0
  heureOuverture=""
  heureFermeture=""
  workingHours=[]
  days = ['Dimanche', 'Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche']
  constructor(private BusinessService: BusinessService, private route: Router, private metaService: Meta, private titleService: Title) { }

  ngOnInit() {


    this.BusinessService.getBusinessByIdMeta(this.getIdFromUrl(this.route.url), "").then(data => {
      this.Business = data.item2
     
    //  console.log("alo alo " + JSON.stringify(data.item2))
    if (this.romdhane){
      this.heureOuverture = data.item2.workingHoursRamadan[this.today.getDay()].hours[0].openTime.substring(0, 5)
      this.heureFermeture = data.item2.workingHoursRamadan[this.today.getDay()].hours[0].closeTime.substring(0, 5)
      this.workingHours = data.item2.workingHoursRamadan
    }else{
      this.heureOuverture = data.item2.workingHours[this.today.getDay()].hours[0].openTime.substring(0, 5)
      this.heureFermeture = data.item2.workingHours[this.today.getDay()].hours[0].closeTime.substring(0, 5)
      this.workingHours = data.item2.workingHours
    }
     
 
      this.metaService.updateTag({ property: 'og:description', content: (data.item2.name + " - " + data.item2.region.label + ", Obtenez menu,avis,numéro de téléphone,contact,adresse,localisation et plus d'informations sur " + data.item2.name + " - " + data.item2.subcategories[0].label + " sur Vynd.") }, `property="og:description"`);
    })



  }


  getIdFromUrl(url) {
    url = url.substring(3, url.length)
    url = url.substring(0, url.indexOf("/"))
    console.log(url)
    return url
  }


}
