import { Component, OnInit, Input } from '@angular/core';
import { BusinessService } from "./../../core/business.service";
import { Router } from '@angular/router';
//import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { PhotoViewComponent } from "../../core/photo-view/photo-view.component";
import { Meta, Title } from "@angular/platform-browser";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { LoadComponent } from "./../../load/load.component";
@Component({
  selector: 'menus',
  templateUrl: './menus.component.html',
  styleUrls: ['./menus.component.css']
})
export class MenusComponent implements OnInit {
  @Input()showMenuText:boolean=true
  constructor(private BusinessService: BusinessService, private metaService: Meta, private titleService: Title, private route: Router,
     private modalService: MatDialog
     ) { }
  Business: any
  id: any;
  menuItems
  busy:Promise<any>
  loading = LoadComponent
  Img = "./../../../assets/img/9erd_far7an.svg"
  ngOnInit() {
    this.busy =  this.BusinessService.getBusinessByIdMeta(this.getIdFromUrl(this.route.url), "").then(data => {
      this.Business = data.item2
      this.metaService.updateTag({ property: 'og:description', content: ("" + data.item2.name + " - voir le menu pour " + data.item2.name + " " + data.item2.region.label + " sur Vynd.") }, `property="og:description"`);
    
    })
    this.busy =   this.BusinessService.getMenuById(this.getIdFromUrl(this.route.url)).then(data => {
      this.menuItems = data.items;
    });



  }

  getIdFromUrl(url) {

    url = url.substring(3, url.length)
    url = url.substring(0, url.indexOf("/"))
   // console.log(url)
    return url
  }

  open_photo(photoUrl: string) {
    const modalRef = this.modalService.open(PhotoViewComponent);
   modalRef.componentInstance.photo = photoUrl;
  }

}
