import {Component, Input, OnInit} from '@angular/core';
//import {NgbActiveModal, NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {BusinessService} from "../business.service";
import {Utils} from "../../shared/utils";
import {SuccessModalComponent} from "../success-modal/success-modal.component";
import {ErreurModalComponent} from "../erreur-modal/erreur-modal.component";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-report-modal',
  templateUrl: './report-modal.component.html',
  styleUrls: ['./report-modal.component.css']
})
export class ReportModalComponent implements OnInit {

  report="";
  @Input() id ;
  errorMsg="";
  
  constructor(
    public activeModal: MatDialogRef<ReportModalComponent> ,
    private businessService:BusinessService,
   private modalService:MatDialog
  ) { }

  ngOnInit() {
  }

  reportComment() {
    if(this.report.length>0) {
      this.businessService.reportComment(this.id,this.report).then(data=> {
        if (Utils.check(data.result)) {
         const success = this.modalService.open(SuccessModalComponent);
          success.componentInstance.texte = 'Merci beaucoup !\n' +
            'Ton rapport est très important N’héistes pas à m’envoyer un report à chaque fois que t’as une remarque.';
          success.componentInstance.photo = '../../../assets/img/9erd_far7an.svg';
          this.activeModal. close();
        }
        else {
          const erreur = this.modalService.open(ErreurModalComponent);
         this.activeModal.close();
        }
      })
        .catch(e=> {
          const erreur = this.modalService.open(ErreurModalComponent);
          this.activeModal.close();
        })
    }
    else {
      this.errorMsg="Le champ est obliatoire !"
    }
  }

}
