import { Component, OnInit } from '@angular/core';
//import {NgbActiveModal} from "@ng-bootstrap/ng-bootstrap";
import {BusinessService} from "../business.service";
import {Tag} from "../../shared/tag.model";
import {Subject} from "rxjs/Subject";
import { AuthentificationService} from "../auth.service";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-search-modal',
  templateUrl: './search-modal.component.html',
  styleUrls: ['./search-modal.component.css']
})
export class SearchModalComponent implements OnInit {
  
  constructor(//public activeModal: NgbActiveModal,
    private businessService: BusinessService, private authService: AuthentificationService,
    public dialogRef: MatDialogRef<SearchModalComponent>) {

    this.searchTermStream
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe((term) => this.filterRegions(term));
  }

  regions:Tag[]=[];
  regionsOrigins:Tag[]=[];
  searchTermStream = new Subject<string>();
  currentLocation:Tag;

  aProx:Tag={
    id :0,
    label : "A proximité",
    type:3,
    parentId: 0
  };

  ngOnInit() {
    this.businessService.getRegions().then(data => {
      for (let i = 0; i < data.items.length; i++) {
        data.items[i].type=3;
        this.regionsOrigins.push(data.items[i]);
      }
      for (let i = 0; i < this.regionsOrigins.length; i++) {
        this.regions.push(this.regionsOrigins[i]);
      }
    });
  }

  filterRegions(term:string){
    this.regions=this.regionsOrigins.filter(function (x) {
      return (x.label.toUpperCase().indexOf(term.toUpperCase()) != -1)
  });
}

clickRegion(region:Tag){
    this.authService.setCurrentLocation(region);
  this.dialogRef.close();
}

detectGps() {
  if(navigator.geolocation){
    navigator.geolocation.getCurrentPosition(position => {
      this.businessService.setLocation(position.coords.latitude.toString(),position.coords.longitude.toString());
      this.authService.setCurrentLocation(this.aProx);
      this.dialogRef.close();

    },error=> {
      alert("Vous devez autoriser l'identification de votre position");
    });
  }
}

}
