import { Component, Input, OnInit, Inject, PLATFORM_ID } from '@angular/core';
import { Business, WorkingHour } from "../business.model";
import { Utils } from "../utils";
import { Comment } from "../comment.model";
import { AuthentificationService } from "../../core/auth.service";
let swal;
if (typeof window !== 'undefined' && typeof document !== 'undefined') {
  swal = require('sweetalert2').default;
}
import { BusinessService } from "../../core/business.service";
import { SearchService } from "../../core/search.service";
import { Router } from "@angular/router";
import { LoggedUserModel } from "../loggedUser.model";
import { ReportModalComponent } from "../../core/report-modal/report-modal.component";
//import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import { ErreurModalComponent } from "../../core/erreur-modal/erreur-modal.component";
import { LoginModalComponent } from "../../core/login-modal/login-modal.component";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { isPlatformBrowser, isPlatformServer } from '@angular/common';
declare var require: any
@Component({
  selector: 'app-comment-item',
  templateUrl: './comment-item.component.html',
  styleUrls: ['./comment-item.component.css']
})
export class CommentItemComponent implements OnInit {

  @Input() public comment: Comment;
  @Input() public type: number;
  userLoggedin: LoggedUserModel = {};


  public displayed = true;
  public showEdit = false;

  public newContent = "";
  constructor(public authService: AuthentificationService, private businessService: BusinessService, private router: Router, private searchService: SearchService,
    private modalService: MatDialog,
    @Inject(PLATFORM_ID) private platformId: Object
  ) {
    this.userLoggedin = this.authService.getCurrentUser();
    this.searchService.notifyUserLoggedIn.subscribe(user => this.userLoggedin = user);
  }



  ngOnInit() {
  }

  createRangeDesc(number) {
    number = 5 - Math.round(number);
    var items: number[] = [];
    for (var i = 1; i <= number; i++) {
      items.push(i);
    }
    return items;

  }
  createRange(number) {
    number = Math.round(number);
    var items: number[] = [];
    for (var i = 1; i <= number; i++) {
      items.push(i);
    }
    return items;
  }

  openTimeToday(workingHours: WorkingHour[]) {
    if (Utils.check(workingHours)) {
      let date = new Date();
      let x = workingHours.find(x => x.dayOfTheWeek == date.getDay());
      if (x) {
        if (x.hours.length > 0) {
          return x.hours;
        }
        else return [];
      }
      return [];
    }
    else {
      return [];
    }

  }

  openTimeDay(workingHours: WorkingHour[], day: number) {
    if (Utils.check(workingHours)) {
      let x = workingHours.find(x => x.dayOfTheWeek == day);
      if (x) {
        if (x.hours.length > 0) {
          return x.hours;
        }
        else return [];
      }
      return [];
    }
    else {
      return [];
    }

  }

  deletePost() {
    let self = this;
    if (isPlatformBrowser(this.platformId)) {
      swal({
        title: 'Êtes-vous sûr?',
        text: "vous voulez supprimer ce commentaire ?",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#63C9D0',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Supprimer',
        cancelButtonText: 'Annuler'
      }).then(function () {
        self.businessService.deleteComment(self.comment.id)
          .then(data => {
            if (Utils.check(data.item2)) {
              self.displayed = false;
              swal(
                'Commentaire supprimé!',
                'Votre commentaire est supprimé',
                'success'
              );
            }
            else {
              const erreur = this.modalService.open(ErreurModalComponent);
            }
          });

      })
    }
  }

  doLike() {
    this.businessService.voteComment(this.comment.id, 1).then(data => {
    });
  }

  doFunny() {
    this.businessService.voteComment(this.comment.id, 3).then(data => {
    });
  }

  doDislike() {
    this.businessService.voteComment(this.comment.id, 2).then(data => {
    });
  }

  clickUtile() {
    if (!this.comment.liked) {
      this.doLike();
      this.comment.nbrLikes++;
      this.comment.liked = true;

      if (this.comment.funny) {
        this.comment.nbrFunny--;
        this.comment.funny = false;
      }
      if (this.comment.disliked) {
        this.comment.nbrDislikes--;
        this.comment.disliked = false;
      }
    }
    else {
      this.doLike();
      this.comment.nbrLikes--;
      this.comment.liked = false;
    }

  }

  clickFunny() {
    if (!this.comment.funny) {
      this.doFunny();
      this.comment.nbrFunny++;
      this.comment.funny = true;

      if (this.comment.liked) {
        this.comment.nbrLikes--;
        this.comment.liked = false;
      }
      if (this.comment.disliked) {
        this.comment.nbrDislikes--;
        this.comment.disliked = false;
      }
    }
    else {
      this.doFunny();
      this.comment.nbrFunny--;
      this.comment.funny = false;
    }
  }

  clickDislike() {
    if (!this.comment.disliked) {
      this.doDislike();
      this.comment.nbrDislikes++;
      this.comment.disliked = true;

      if (this.comment.liked) {
        this.comment.nbrLikes--;
        this.comment.liked = false;
      }
      if (this.comment.funny) {
        this.comment.nbrFunny--;
        this.comment.funny = false;
      }
    }
    else {
      this.doDislike();
      this.comment.nbrDislikes--;
      this.comment.disliked = false;
    }
  }

  doModifyComment() {
    this.businessService.modifyComment(this.comment.id, this.newContent)
      .then(data => {
        if (Utils.check(data.item2)) {
          this.comment.content = this.newContent;
          this.showEdit = false;
        }
        else {
          const erreur = this.modalService.open(ErreurModalComponent);
        }
      });
  }

  goToBusiness(business: Business) {
    this.router.navigate(['v/' + business.id + '/' + business.metaIdentifier]);
  }

  doReply(x) {
    if (x.value.length > 0) {
      this.businessService.replyComment(this.comment.id, x.value)
        .then(data => {
          this.comment.response.push(data);
        });
      x.value = "";
    }

  }

  openReport(id: number) {
    const modalRef = this.modalService.open(ReportModalComponent);
    modalRef.componentInstance.id = id;
  }

  goToUser(url: string) {
    if (this.authService.isUserLoggedIn()) {
      this.router.navigate(['/user/' + url])
    }
    else {
      const modalRef = this.modalService.open(LoginModalComponent);
    }
  }

  iSMobile(): boolean {
    var check = false;
    (function (a) { if (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino|android|ipad|playbook|silk/i.test(a) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0, 4))) check = true; })(navigator.userAgent || navigator.vendor);
    return check;
  };


}
