import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ProfileUserComponent} from "./profile-user.component";
import {ProfileUserCommentsComponent} from "./profile-user-comments/profile-user-comments.component";
import {ProfileUserSubscribersComponent} from "./profile-user-subscribers/profile-user-subscribers.component";
import {ProfileUserSubscriptionsComponent} from "./profile-user-subscriptions/profile-user-subscriptions.component";

const routes: Routes = [
  {
    path: 'user/:id',
    component:ProfileUserComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'comments'
      },
      {
        path: 'subscriptions',
        component: ProfileUserSubscriptionsComponent
      },
      {
        path: 'comments',
        component: ProfileUserCommentsComponent
      },
      {
        path: 'subscribers',
        component: ProfileUserSubscribersComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileUserRoutingModule { }
