import {Output, EventEmitter} from '@angular/core';
import {LoggedUserModel} from "../shared/loggedUser.model";

export class SearchService {
  @Output() notifySearchResult: EventEmitter<any> = new EventEmitter();
  @Output() notifyRegionSearch: EventEmitter<any> = new EventEmitter();
  @Output() notifyInputSearch: EventEmitter<any> = new EventEmitter();
  @Output() notifyEmptyComment: EventEmitter<any> = new EventEmitter();
  @Output() notifyLoginLoading: EventEmitter<any> = new EventEmitter();
  @Output() notifyLoginLoadingOff: EventEmitter<any> = new EventEmitter();
  @Output() notifyEmptyCommentProfileBusiness: EventEmitter<any> = new EventEmitter();
  @Output() notifYProfileBusinessPushComment: EventEmitter<any> = new EventEmitter();
  @Output() notifyUserLoggedIn: EventEmitter<any> = new EventEmitter();

  setSearch(){
    this.notifySearchResult.emit("hello !");
  }
  setRegionInput(inputText:string){
    this.notifyRegionSearch.emit(inputText);
  }
  setInputSearch(inputText:string){
    this.notifyInputSearch.emit(inputText);
  };
  emptyComment(){
    this.notifyEmptyComment.emit("hello");
  };

  emptyCommentProfileBusiness(){
    this.notifyEmptyCommentProfileBusiness.emit("hello");
  };

  setLoginLoading() {
    this.notifyLoginLoading.emit("hello");
  }
  setLoginLoadingOff() {
    this.notifyLoginLoadingOff.emit("hello");
  }
  pushCommentProfileBusiness(comment:Comment) {
    this.notifYProfileBusinessPushComment.emit(comment);
  }

  userLoggedIn(user:LoggedUserModel) {
    this.notifyUserLoggedIn.emit(user);
  }
}
