import { Component, OnInit } from '@angular/core';
//import {NgbActiveModal, NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {BusinessService} from "../business.service";
import {Utils} from "../../shared/utils";
import {SuccessModalComponent} from "../success-modal/success-modal.component";
import {ErreurModalComponent} from "../erreur-modal/erreur-modal.component";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-feedback-modal',
  templateUrl: './feedback-modal.component.html',
  styleUrls: ['./feedback-modal.component.css']
})
export class FeedbackModalComponent implements OnInit {

  feedback_icons=[
    {
      "label" : "Déçu !",
      "icon" : "../../../assets/img/decu.svg",
      "icon_active" : "../../../assets/img/decu_activated.svg",
      "active" : false
    },
    {
      "label" : "Bof !",
      "icon" : "../../../assets/img/bof.svg",
      "icon_active" : "../../../assets/img/bof_activated.svg",
      "active" : false
    },
    {
      "label" : "Normal",
      "icon" : "../../../assets/img/normal.svg",
      "icon_active" : "../../../assets/img/normal_activated.svg",
      "active" : false
    },
    {
      "label" : "cool !",
      "icon" : "../../../assets/img/cool.svg",
      "icon_active" : "../../../assets/img/cool_activated.svg",
      "active" : false
    },
    {
      "label" : "J'adore !",
      "icon" : "../../../assets/img/jadore.svg",
      "icon_active" : "../../../assets/img/jadore_activated.svg",
      "active" : false
    }
  ];
  feedback="";
  selected=0;
  errorMsg="";
  activeModal: any
  constructor(//public activeModal: NgbActiveModal,
    private businessService:BusinessService,
   private modalService:MatDialog,
    public dialogRef: MatDialogRef<FeedbackModalComponent>
  )
     { }

  ngOnInit() {
  }

  clickedIcon(icon){
    for(let i=0;i<this.feedback_icons.length;i++){
      this.feedback_icons[i].active=false;
    }
    icon.active=true;
    this.selected=this.feedback_icons.indexOf(icon)+1;
  }

  sendFeedback(){
    if(this.selected!=0&&this.feedback.length>0) {
      this.businessService.sendFeedback(this.selected,this.feedback).then(data=> {
        if (Utils.check(data.createDate)) {
          const success = this.modalService.open(SuccessModalComponent);
          success.componentInstance.texte = 'Merci beaucoup !\n' +
            'Ton feedback est très important N’héistes pas à m’envoyer ton feedback à chaque fois que t’as une remarque.';
          success.componentInstance.photo = '../../../assets/img/9erd_far7an.svg';
          this.dialogRef.close();
        }
        else {
          const erreur = this.modalService.open(ErreurModalComponent);
          this.dialogRef.close();
        }
      })
    }
    else {
      this.errorMsg="Tous les champs sont obligatoires !"
    }
  }

}
