import { Component, OnInit } from '@angular/core';
import {BusinessService} from "../../core/business.service";
import {Comment} from "../../shared/comment.model";
import {Business} from "../../shared/business.model";
import {ActivatedRoute, Router} from "@angular/router";
import { AuthentificationService} from "../../core/auth.service";
import {LoggedUserModel} from "../../shared/loggedUser.model";
import {SearchService} from "../../core/search.service";

@Component({
  selector: 'app-profile-user-comments',
  templateUrl: './profile-user-comments.component.html',
  styleUrls: ['./profile-user-comments.component.css']
})
export class ProfileUserCommentsComponent implements OnInit {

  comments: Comment[] = [];
  userLoggedin:LoggedUserModel={};
  public paginationParameters = {
    offset: 0,
    limit: 5,
    size: 1,
    page: 1
  };
  id=0;

  constructor(private businessService: BusinessService, private router: Router, private route: ActivatedRoute, private authService: AuthentificationService,private searchService:SearchService) {
    this.searchService.notifyUserLoggedIn.subscribe(user => this.userLoggedin=user);
  }

  ngOnInit(){
    this.route.parent.params.subscribe(params => {
      this.businessService.getCommentsByUser(params['id'],0,5).then(data=> {
        this.comments=data.items;
        this.paginationParameters.size=data.size;
        this.id=params['id'];
        
      })
    });
  }

  createRangeDesc(number){
    number = 5-Math.round(number);
    var items: number[] = [];
    for (var i = 1; i <= number; i++) {
      items.push(i);
    }
    return items;

  }
  createRange(number) {
    number = Math.round(number);
    var items: number[] = [];
    for (var i = 1; i <= number; i++) {
      items.push(i);
    }
    return items;
  }

  goToBusiness(business: Business) {
    this.router.navigate(['v/' + business.id + '/' + business.metaIdentifier]);
  }

  loadComments(page) {
    this.paginationParameters.page = page;
    this.paginationParameters.offset = (page - 1) * this.paginationParameters.limit;
    if (this.paginationParameters.offset < this.paginationParameters.size) {
      this.businessService.getCommentsByUser(this.id, this.paginationParameters.offset, this.paginationParameters.limit)
        .then(data => {
          this.comments=[];
          this.comments=data.items;

        });
    }
  }
}
