import { Component, OnInit ,Input } from '@angular/core';
import { BusinessService } from "./../../core/business.service";
import { Router } from '@angular/router';
//import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { PhotoViewComponent } from "../../core/photo-view/photo-view.component";
import { Meta, Title } from "@angular/platform-browser";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { LoadComponent } from "./../../load/load.component";

@Component({
  selector: 'avis',
  templateUrl: './avis.component.html',
  styleUrls: ['./avis.component.css']
})
export class AvisComponent implements OnInit {
@Input() changeMetaDescription:boolean=true
  constructor(private businessService: BusinessService, private route: Router,
     private modalService:MatDialog ,
      private metaService: Meta, private titleService: Title) { }
  comments
  BusinessId
  busy: Promise<any>;
  loading = LoadComponent
  ngOnInit() {
    this.BusinessId = this.getIdFromUrl(this.route.url)

    this.busy =  this.businessService.getBusinessByIdMeta(this.getIdFromUrl(this.route.url), "").then(data => {
     
     // console.log("alo alo " + JSON.stringify(data.item2))
      if (!this.changeMetaDescription)
      { this.metaService.updateTag({ property: 'og:description', content: (data.item2.name + " - " + data.item2.region.label + ", Obtenez menu,avis,numéro de téléphone,contact,adresse,localisation et plus d'informations sur " + data.item2.name + " - " + data.item2.subcategories[0].label + " sur Vynd.") }, `property="og:description"`);}
    else  { this.metaService.updateTag({ property: 'og:description', content: ("Avis des utilisateurs pour " + data.item2.name + " - " + data.item2.region.label + " - " + data.item2.subcategories[0].label + " sur Vynd.") }, `property="og:description"`);}
    
    })


  this.busy =  this.businessService.getComments(this.getIdFromUrl(this.route.url)).then(data => {
      this.comments = data.items;
       {
        for (let i = 0; i < data.items.length; i++) {
          if (data.items[i].id ==0) {
            this.comments.push(data.items[i]);
          }
        }
     
      }
    })

  }


  getIdFromUrl(url) {
    url = url.substring(3, url.length)
    url = url.substring(0, url.indexOf("/"))
 //   console.log(url)
    return url
  }

  open_photo(photoUrl: string) {
    const modalRef = this.modalService.open(PhotoViewComponent);
   modalRef.componentInstance.photo = photoUrl;
  }

}
