import { EventEmitter, Injectable, Output, Inject } from '@angular/core';
import { Utils } from "../shared/utils";
import { Router } from "@angular/router";
import { User } from "../shared/user.model";
import { LoggedUserModel } from "../shared/loggedUser.model";
import { Tag } from "../shared/tag.model";
import { SearchTag } from "../shared/searchParams.model";
import { PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser, isPlatformServer } from '@angular/common';
@Injectable()
export class AuthentificationService {

  userHasLoggedin: EventEmitter<LoggedUserModel> = new EventEmitter<LoggedUserModel>();
  @Output() notifYProfileBusinessLoginUser: EventEmitter<any> = new EventEmitter();
  @Output() changeLocation: EventEmitter<any> = new EventEmitter();



  constructor(public router: Router, @Inject(PLATFORM_ID) private platformId: Object) {

  }
  getCurrentLocation(): SearchTag {
    if (isPlatformBrowser(this.platformId)) {
      if (Utils.check(localStorage.getItem('current_location'))) {
        return JSON.parse(localStorage.getItem('current_location'));
      }
      else {
        let location = {
          id: 90,
          label: "Tunis",
          type: 3
        };
        return location;
      }
    }
  }

  setCurrentLocation(location: Tag) {
    if (isPlatformBrowser(this.platformId)) {
      localStorage.setItem('current_location', JSON.stringify(location));
      this.changeLocation.emit(location);
    }
  }

  getCurrentUser(): User {
    if (isPlatformBrowser(this.platformId)) {
      if (this.isUserLoggedIn()) {
        return JSON.parse(localStorage.getItem('current_vyndee'));
      }
      return null;
    }
  }

  setUserCredentials(user: any): void {
    if (isPlatformBrowser(this.platformId)) {
      if (user.item2) {
        localStorage.setItem('current_vyndee', JSON.stringify(user.item2));
        this.notifYProfileBusinessLoginUser.emit(user.item2);
      }
      else {
        localStorage.setItem('current_vyndee', JSON.stringify(user));
      }
      this.userHasLoggedin.emit(user);
    }
  }

  isUserLoggedIn() {
    if (isPlatformBrowser(this.platformId)) {
      return Utils.check(localStorage.getItem('current_vyndee'));
    }

  }

  logoutUser() {
    if (isPlatformBrowser(this.platformId)) {
      localStorage.clear();
      this.router.navigate(['/']);
      this.userHasLoggedin.emit(null);
    }
  }
}
