import { Component, OnInit } from '@angular/core';
//import {NgbActiveModal} from "@ng-bootstrap/ng-bootstrap";
import { Category, Tag } from "../../shared/tag.model";
import { BusinessService } from "../business.service";
import { Business } from "../../shared/business.model";
import { Subject } from "rxjs/Subject";
import { Router } from "@angular/router";
import { SearchParams, SearchTag } from "../../shared/searchParams.model";
import { AuthentificationService } from "../auth.service";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-keyword-modal',
  templateUrl: './keyword-modal.component.html',
  styleUrls: ['./keyword-modal.component.css']
})
export class KeywordModalComponent implements OnInit {

  currentLocation: SearchTag;
  searchTermStream = new Subject<string>();
  categories: Category[] = [];
  showCategories: Category[] = [];
  businesses: Business[] = [];
  searchBody: SearchParams = {
    "tags": [],
    "latitude": "",
    "longitude": "",
    "distance": 0,
    "open": 0,
    "coupon": 0,
    "popularity": 0,
    "note": 0,
    "budget": 0,
    "searchId": ""
  };

  constructor(
    public activeModal: MatDialogRef<KeywordModalComponent>,
    private businessService: BusinessService, private router: Router, private authService: AuthentificationService,
    public dialogRef: MatDialogRef<KeywordModalComponent>) {
    this.searchTermStream
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe((term) => this.autoComplete(term));
  }

  ngOnInit() {
    this.businessService.getSubCategories().then(data => {
      for (let i = 0; i < data.items.length; i++) {
        this.categories.push(data.items[i]);
        this.categories[i].type = 2;
        this.showCategories.push(this.categories[i]);
      }
    });
    this.currentLocation = this.authService.getCurrentLocation();
  }

  autoComplete(term: string) {
    this.showCategories = this.categories.filter(function (category) {
      return (category.label.toUpperCase().indexOf(term.toUpperCase()) != -1);
    });
    if (term != "") {
      if (term.startsWith("#")) {
        this.businesses = [];
      }
      else {
        this.businessService.aucomplete(term).then(data => {
          this.businesses = data.items;
        });
      }
    }
    else {
      this.businesses = [];
    }
  }

  goToBusiness(business: Business) {
    this.router.navigate(['v/' + business.id + '/' + business.metaIdentifier]);
    this.dialogRef.close();
  }

  goToCategory(category: SearchTag) {
    let x = this.searchBody.tags.find(x => x.type == 0);
    if (x) {
      this.searchBody.tags.splice(this.searchBody.tags.indexOf(x), 1);
    }
    this.searchBody.tags.push(category);
    this.searchBody.tags.push(this.currentLocation);
    localStorage.setItem("search", JSON.stringify(this.searchBody));
    let s = localStorage.getItem("search");
    this.router.navigate(['searchresult']);
    this.dialogRef.close();
  }

}
