  checkAccountFacebook(){
    this.businessService.loginSocial(0,this.facebookUser)
      .then(data=> {

        if (Utils.check(data.firstName)) {
          this.authService.setUserCredentials(data);
          setTimeout(() => {
            this.busy=this.businessService.postComment(this.comment,this.authService.getCurrentUser().token).then(data=> {
              if(Utils.check(data.item2)){
               /* const succees = this.modalService.open(SuccessModalComponent);
                succees.componentInstance.texte = 'Merci '+[this.authService.getCurrentUser().firstName]+' ! Cet avis va aider nos amis Vynders à bien choisir où sortir.';
                succees.componentInstance.photo = '../../../assets/img/9erd_love.svg';
                this.comment={};
                this.searchService.setLoginLoadingOff();
                this.searchService.emptyComment();
                this.searchService.emptyCommentProfileBusiness();*/
              }
              else {
               const erreur = this.modalService.open(ErreurModalComponent);
                this.searchService.setLoginLoadingOff();

              }
            });
             }, 1000);
        }
        else {
          const erreur = this.modalService.open(ErreurModalComponent);
          this.searchService.setLoginLoadingOff();
        }
      })
    ;
  }

  attachSignin() {
    let self = this;
    setTimeout(function () {
      gapi.load('auth2', () => {
        this.auth2 = gapi.auth2.init({
          client_id: '670012671696-uih5jacndb96bqpokar4e2kdlt855gbh.apps.googleusercontent.com',
          cookiepolicy: 'single_host_origin',
          scope: 'https://www.googleapis.com/auth/plus.me https://www.googleapis.com/auth/plus.login https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/userinfo.profile profile email'
        });

        var elem1 = document.getElementById('signInGplus1');
        if(elem1!=null){
          this.auth2.attachClickHandler(elem1, {},
            (loggedInUser) => {
              self.searchService.setLoginLoading();
              self.googleplusUser.id = loggedInUser.w3.Eea;
              self.googleplusUser.prenom = loggedInUser.w3.ofa;
              self.googleplusUser.nom= loggedInUser.w3.wea;
              self.googleplusUser.profilePic = loggedInUser.w3.Paa;
              self.googleplusUser.email=loggedInUser.w3.U3;
              self.checkAccountGooglePlus();

            }, function (error) {
              const erreur = this.modalService.open(ErreurModalComponent);
              self.searchService.setLoginLoadingOff();
            });
        }
      });

    },0);
  }

  checkAccountGooglePlus(){
    let self = this;

    this.businessService.loginSocial(1,this.googleplusUser)
      .then(data=> {

        if (Utils.check(data.firstName)) {
          this.authService.setUserCredentials(data);
          setTimeout(() => {
            this.businessService.postComment(this.comment,this.authService.getCurrentUser().token).then(data=> {
              if(Utils.check(data.item2)){
                const succees = this.modalService.open(SuccessModalComponent);
                succees.componentInstance.texte = 'Merci '+[this.authService.getCurrentUser().firstName]+' ! Cet avis va aider nos amis Vynders à bien choisir où sortir.';
                succees.componentInstance.photo = '../../../assets/img/9erd_love.svg';
                this.comment={};
                this.searchService.emptyComment();
                this.searchService.emptyCommentProfileBusiness();
                self.searchService.setLoginLoadingOff();
              }
              else {
               const erreur = this.modalService.open(ErreurModalComponent);
                self.searchService.setLoginLoadingOff();
              }
            });
              }, 1000);
        }
        else {
         const erreur = this.modalService.open(ErreurModalComponent);
          self.searchService.setLoginLoadingOff();
        }
      })
    ;
  }


