import { Component, OnInit } from '@angular/core';
import {User} from "../shared/user.model";
import {BusinessService} from "../core/business.service";
import {ActivatedRoute} from "@angular/router";
import { AuthentificationService} from "../core/auth.service";
import {ProfileUserModel} from "../shared/profileUser.model";
import {Comment} from "../shared/comment.model";
import {Utils} from "../shared/utils";
import { Meta, Title } from "@angular/platform-browser";
@Component({
  selector: 'app-profile-user',
  templateUrl: './profile-user.component.html',
  styleUrls: ['./profile-user.component.css']
})
export class ProfileUserComponent implements OnInit {

  user:ProfileUserModel={};
  comments:Comment[];
  public paginationParameters = {
    offset: 0,
    limit: 5,
    size: 1,
    page: 1
  };

  constructor(private titleService: Title, private metaService: Meta, private businessService: BusinessService, private route: ActivatedRoute, public authService: AuthentificationService) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.businessService.getUser(params["id"],this.authService.getCurrentUser().token).then(data=> {
        this.user=data.item2;
        this.titleService.setTitle(data.item2.firstName + " " + data.item2.lastName  + " | Vynd ");
        this.metaService.updateTag({ property: 'og:title', content: data.item2.firstName + " " + data.item2.lastName + " " + data.item2.region + " | Vynd " }, `property="og:title"`);

        this.metaService.updateTag({ property: 'og:description', content: (" le profil Vynder de " + data.item2.firstName + " " + data.item2.lastName + " sur vynd , consultez les avis de " + data.item2.firstName + " " + data.item2.lastName + " sur les restaurants, les coffee shops, les bars, les fast foods, les pizzeria, les clubs et lounges et plus encore sur vynd. Suivez " + data.item2.firstName + " " + data.item2.lastName + " sur Vynd ") }, `property="og:description"`);
      })
    });
   



  }

  followUser(id:number){
    this.businessService.followUser(id).then(data=>{
      if (Utils.check(data.status==200)) {
        this.user.isFollowed=!this.user.isFollowed;
      }
    })
  }



}
