import { Component ,Inject } from '@angular/core';
import './operators' ;
import {ActivatedRoute, NavigationEnd, Router} from "@angular/router";
//import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {FeedbackModalComponent} from "./core/feedback-modal/feedback-modal.component";
import {SearchService} from "./core/search.service";
import { AuthentificationService} from "./core/auth.service";
import {BusinessService} from "./core/business.service";

import { PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
declare let ga:any;
declare  let Smooch:any;
declare  let fbq:any;
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  show_searchBar=false;
  loadingLogin=false;
  show_footer = true
  constructor(private authService: AuthentificationService,private businessService:BusinessService,private searchService:SearchService,router: Router, activatedRoute: ActivatedRoute,
    
    @Inject(PLATFORM_ID) private platformId: Object
  ) {
    if (this.authService.isUserLoggedIn()) {
      this.businessService.getNewToken(this.authService.getCurrentUser().token)
        .then(data => {
          let u = this.authService.getCurrentUser();
          u.token = data.token;
          this.authService.setUserCredentials(u);
        });
    }
    this.searchService.notifyLoginLoading.subscribe(test => this.loadingLogin=true);
    this.searchService.notifyLoginLoadingOff.subscribe(test => this.loadingLogin=false);
  /*  Smooch.init({
      appId: '59dc124472274c00340ed8fd'
    });*/

    router.events.filter(e => e instanceof NavigationEnd).subscribe((event) => {
          if (event instanceof NavigationEnd){
            if (isPlatformBrowser(this.platformId)) {
           ga('send', 'pageview', event.urlAfterRedirects);
            fbq('trackCustom', 'PageView', { page: event.urlAfterRedirects });
            }
            if (event.urlAfterRedirects == '/'){
              this.show_searchBar=false;
              this.show_footer = true
            }
            else {
              console.log("now url=" + event.url )
              if (event.url == '/Download-App-Vynd'){
                this.show_footer = false
                this.show_searchBar = false;
              }else
              {
                this.show_footer = true
                this.show_searchBar = true;
              }
           
            
            }
          }
        });
  }
}
