import { Component, OnInit } from '@angular/core';
import { BusinessService } from "../core/business.service";
import { User } from "../shared/user.model";
import { Router } from "@angular/router";
import { AuthentificationService } from "../core/auth.service";
import { LoginModalComponent } from "../core/login-modal/login-modal.component";
//mport {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { LoadComponent } from "./../load/load.component";
@Component({
  selector: 'app-leaderboard',
  templateUrl: './leaderboard.component.html',
  styleUrls: ['./leaderboard.component.css']
})
export class LeaderboardComponent implements OnInit {

  users: User[];
  public paginationParameters = {
    offset: 0,
    limit: 10,
    size: 1,
    page: 1
  };

  busy: Promise<any>;
  loading = LoadComponent
  constructor(private businessService: BusinessService, private router: Router, private authService: AuthentificationService,
    private modalService: MatDialog
  ) { }

  ngOnInit() {
    this.busy = this.businessService.getLeaderBoard(this.paginationParameters.offset, this.paginationParameters.limit).then(data => {
      this.users = data.items;
    })

  }


  onShowMore() {
    let page = this.paginationParameters.page++;
    this.paginationParameters.offset = (page) * this.paginationParameters.limit;
    if (true) {
      this.busy = this.businessService.getLeaderBoard(this.paginationParameters.offset, this.paginationParameters.limit)
        .then(data => {
          for (let i = 0; i < data.items.length; i++) {
            this.users.push(data.items[i]);
          }
        });
    }

  }

  goToUser(id: number) {
    if (this.authService.isUserLoggedIn()) {
      this.router.navigate(['/user/' + id])
    }
    else {
      const modalRef = this.modalService.open(LoginModalComponent);
    }
  }

}
