import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
  name: 'dateComment'
})
export class DateCommentPipe implements PipeTransform {

  transform(d: string,type:number=0): string {
    let dates = d.substr(6,4)+'-'+d.substr(3,2)+'-'+d.substr(0,2)+"T"+d.substr(11,5)+':10Z';
    let date  = new Date(dates);


    let now : Date = new Date(Date.now());
    if (date.getUTCFullYear() == now.getUTCFullYear()){
      if (date.getUTCMonth() == now.getUTCMonth()){
        if (date.getUTCDate() == now.getUTCDate()){
          if (date.getUTCHours() == now.getUTCHours()){
            if ((now.getMinutes() - date.getMinutes())<2) {
              return "Maintenant";
            }
            else {
              if(type==0)
                return (now.getUTCMinutes()-date.getUTCMinutes())+" mins";
              else
                return (now.getUTCMinutes()-date.getUTCMinutes())+" m";
            }
          }
          else if(now.getUTCHours()-date.getUTCHours()==1) {
            if(type==0)
              return "1 hr";
            else
              return "1 h";
          }
          else if(now.getUTCHours()-date.getUTCHours() < 5){
            if(type==0)
            return (now.getUTCHours()-date.getUTCHours())+" hrs";
            else
              return (now.getUTCHours()-date.getUTCHours())+" h";
          }
          else {
            return format(now.getUTCHours())+":"+format(date.getUTCMinutes());
          }
        }
        else if(date.getUTCDate() == now.getUTCDate()-1){
          return "Hier "+format(now.getUTCHours())+":"+format(date.getUTCMinutes());
        }
        else if(now.getUTCDate()-date.getUTCDate()<=7){
          if(type==0)
            return (now.getUTCDate()-date.getUTCDate())+"jours ";
          else
            return (now.getUTCDate()-date.getUTCDate())+"j ";
        }
        else {
          if(type==0)
          return date.getUTCDate()+" "+this.getNameOfMonth(date.getUTCMonth()+1);
          else
            return date.getUTCDate()+" "+this.getNameOfMonth(date.getUTCMonth()+1);
        }
      }
      else {
        if(type==0)
          return date.getUTCDate()+" "+this.getNameOfMonth(date.getUTCMonth()+1);
        else
          return date.getUTCDate()+" "+this.getNameOfMonth(date.getUTCMonth()+1);
      }
    }
    else {
      if(type==0)
      return date.getUTCDate()+" "+this.getNameOfMonth(date.getUTCMonth()+1)+" "+date.getUTCFullYear();
      else
        return date.getUTCDate()+" "+this.getNameOfMonth(date.getUTCMonth()+1)+" "+date.getUTCFullYear();
    }
  }
  getNameOfMonth(numMonth : number ) {
    switch (numMonth) {
      case 1 : return "Jan";
      case 2 : return "Fev";
      case 3 : return "Mar";
      case 4 : return "Avril";
      case 5 : return "Mai";
      case 6 : return "Juin";
      case 7 : return "Juil";
      case 8 : return "Aout";
      case 9 : return "Sep";
      case 10 : return "Oct";
      case 11 : return "Nov";
      case 12 : return "Dec";
    }
    return "";
  }

}
function format(n){
  return n > 9 ? "" + n: "0" + n;
}
