import { NgModule, Optional, SkipSelf } from '@angular/core';
import { BusinessService } from "./business.service";
import { Routes, RouterModule } from '@angular/router';
import { SearchService } from "./search.service";
import { DownloadModalComponent } from "./download-modal/download-modal.component";
import { PhotoViewComponent } from "./photo-view/photo-view.component";
import { LoginModalComponent } from './login-modal/login-modal.component';
import { AuthentificationService } from "./auth.service";
import { ErreurModalComponent } from "./erreur-modal/erreur-modal.component";
import { SuccessModalComponent } from './success-modal/success-modal.component';
import { LoginCommentModalComponent } from './login-comment-modal/login-comment-modal.component';
import { FeedbackModalComponent } from './feedback-modal/feedback-modal.component';
import { ReportModalComponent } from './report-modal/report-modal.component';
import { SearchModalComponent } from './search-modal/search-modal.component';
import { KeywordModalComponent } from './keyword-modal/keyword-modal.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { LandingPageComponent } from './landing-page/landing-page.component';
import { SearchParamsService } from "./search-params.service";
import { SocialLoginModule, AuthServiceConfig } from "angularx-social-login";
import { GoogleLoginProvider, FacebookLoginProvider, LinkedInLoginProvider, LoginOpt} from "angularx-social-login";
import { MatButtonModule } from '@angular/material/button'
import { MatTooltipModule } from '@angular/material/tooltip';
import {

  MatDatepickerModule,
  MatDialogModule,
  MatInputModule, MatListModule, MatPaginatorModule, MatProgressSpinnerModule, MatSelectModule, MatSidenavModule,
  MatSortModule,
  MatTableModule,
  MatToolbarModule
} from "@angular/material";

const routes: Routes = [
  {
    path: 'Download-App-Vynd',
    component: LandingPageComponent,
  }
];
const googleLoginOptions: LoginOpt = {
  scope: 'https://www.googleapis.com/auth/plus.me https://www.googleapis.com/auth/plus.login https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/userinfo.profile profile email'
};

let config = new AuthServiceConfig([
  {
    id: GoogleLoginProvider.PROVIDER_ID,
    provider: new GoogleLoginProvider("670012671696-uih5jacndb96bqpokar4e2kdlt855gbh.apps.googleusercontent.com", googleLoginOptions)
  },
  {
    id: FacebookLoginProvider.PROVIDER_ID,
    provider: new FacebookLoginProvider("1605782476360596")
  },

]);


export function provideConfig() {
  return config;
}

@NgModule({
  providers: [BusinessService, SearchService,AuthentificationService,SearchParamsService, {
    provide: AuthServiceConfig,
    useFactory: provideConfig
  }],
  entryComponents: [KeywordModalComponent, SearchModalComponent, DownloadModalComponent, PhotoViewComponent, LoginModalComponent, ErreurModalComponent, SuccessModalComponent, LoginCommentModalComponent, FeedbackModalComponent, ReportModalComponent],
  declarations: [ContactUsComponent, LandingPageComponent,],
  imports: [
    SocialLoginModule,
    MatDialogModule,
    MatButtonModule,
    MatTooltipModule,
    RouterModule.forRoot(
      routes,
      
    )
  ]
})
export class CoreModule {

  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    if (parentModule) {
      throw new Error(
        'CoreModule is already loaded. Import it in the AppModule only');
    }
  }

}
