  open() {
    const modalRef = this.modalService.open(DownloadModalComponent);
    //   modalRef.componentInstance.name = 'World';
  }

  onScroll() {
    if (this.iSMobile()) {
      console.log("scrolling");
      if (!this.searching) {
        console.log("scrolling-1");
        this.searching = true;
        this.paginationParameters.page++;
        this.paginationParameters.offset = (this.paginationParameters.page - 1) * this.paginationParameters.limit;
        if (this.paginationParameters.offset < this.paginationParameters.size) {
          this.positions.splice(0, this.positions.length);
          this.newSearchState = false;
          this.busy = this.businessService.searchFull(this.searchBody, this.paginationParameters.offset, this.paginationParameters.limit)
            .then(data => {

              for (let i = 0; i < data.items.length; i++) {
                this.businesses.push(data.items[i]);
              }

              this.positions.splice(0, this.positions.length);
              data.items.filter(info => {
                this.positions.push({ "lat": parseFloat(info.latitude), "lng": parseFloat(info.longitude) })
              })
              this.searching = false;
            });
        }
      }
    }


  }

