  onMouseOver(infoWindow, gm, position) {
    //i used newSearchState i just need that 
    if (this.newSearchState) {
      if (gm.lastOpen != null) {
        gm.lastOpen.close();
      }
    }


    gm.lastOpen = infoWindow;
    // console.log("length of postions table " + this.positions.length)
    for (let i = 0; i < this.positions.length; i++) {
      if ((position.lng == this.positions[i].lng) && (position.lat == this.positions[i].lat)) {
        this.BusinessOnMap = this.businesses[i]
        // console.log("MouseOver" + JSON.stringify(this.businesses[i]))   
      }
    }
    this.newSearchState = true
    infoWindow.open();
  }

