import { Component, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { Business } from "../../shared/business.model";
import { Tag } from "../../shared/tag.model";
import { BusinessService } from "../business.service";
import { SearchParams } from "../../shared/searchParams.model";
import { ActivatedRoute, Router } from "@angular/router";
import { Subject } from "rxjs/Subject";
import { SearchService } from "../search.service";
import { DownloadModalComponent } from "../download-modal/download-modal.component";
//import {NgbDropdownConfig, NgbModal} from "@ng-bootstrap/ng-bootstrap";
import { LoginModalComponent } from "../login-modal/login-modal.component";
import { AuthentificationService } from "../auth.service";
import { LoggedUserModel } from "../../shared/loggedUser.model";
import { trigger, transition, style, animate, state } from '@angular/animations'
import { Meta, Title } from "@angular/platform-browser";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatButtonModule } from '@angular/material/button';

import { LoadComponent } from "./../../load/load.component";

@Component({
  selector: 'app-header-search',
  animations: [
    trigger(
      'myAnimation',
      [
        transition(
          ':enter', [
            style({ transform: 'translateY(100%)', opacity: 0 }),
            animate('500ms', style({ transform: 'translateY(0)', 'opacity': 1 }))
          ]
        ),
        transition(
          ':leave', [
            style({ transform: 'translateX(0)', 'opacity': 1 }),
            animate('500ms', style({ transform: 'translateX(100%)', 'opacity': 0 }))
          ]
        )]
    )
  ],
  templateUrl: './header-search.component.html',
  styleUrls: ['./header-search.component.css']
})
export class HeaderSearchComponent implements OnInit {

  @ViewChild('regionSelect') regionSelect;

  userLoggedin: LoggedUserModel = {};
  show_autocomplete = false;
  show_autocomplete_box = false;
  searchTermStream = new Subject<string>();
  loading = LoadComponent
  busy_search: Promise<any>;
  subCategories: Tag[] = [];
  inputText = "";
  businesses: Business[] = [];
  regions: Tag[] = [];
  regionsOrigin: Tag[] = [];
  allCategories: Tag[] = [];
  categories: Tag[] = [];
  searchBody: SearchParams = {
    "tags": [],
    "latitude": "",
    "longitude": "",
    "distance": 0,
    "open": 0,
    "coupon": 0,
    "popularity": 0,
    "note": 0,
    "budget": 0
  };
  searchParams: Tag[] = [];
  showCategories: Tag[] = [];

  selectedRegion: any;

  aProx: Tag = {
    id: 0,
    label: "A proximité",
    type: 3,
    parentId: 0
  };
  currentRegion = "";
  currentInput = "";

  showSearchResponsive = false;
  nbAvis: number;
  nbBusinesses: number


  match_keyword = {
    "id": 0,
    "parentId": 0,
    "type": 0,
    "label": "#match"
  };

  constructor(private businessService: BusinessService, private route: ActivatedRoute, private router: Router,
    private searchService: SearchService, private metaService: Meta, private titleService: Title,
    private modalService: MatDialog,
    //private config: NgbDropdownConfig,
    public dialog: MatDialog,
    private authService: AuthentificationService) {

    this.searchService.notifyRegionSearch.subscribe(test => this.putRegion(test));
    this.searchService.notifyInputSearch.subscribe(test => this.putInputText(test));
    //  config.placement = 'bottom-right';

    this.searchTermStream
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe((term) => this.autoComplete(term));

    this.authService.userHasLoggedin.subscribe(data => {
      this.userLoggedin = data;
    });
  }

  ngOnInit() {


    /*if (this.businessService.getLocation().longitude!="0") {
      this.regionsOrigin.unshift(this.aProx);
    }*/
    this.userLoggedin = this.authService.getCurrentUser();
    this.businessService.getRegions().then(data => {
      for (let i = 0; i < data.items.length; i++) {
        data.items[i].type = 3;
        this.regionsOrigin.push(data.items[i]);

      }
      this.regions = this.regionsOrigin;
      if (this.currentRegion.length > 0) {
        let x = this.regions.find(x => x.label == this.currentRegion);
        if (x) {
          let a = [x];
          this.selectedRegion = a;
        }
      }
    });
    this.businessService.getCategories().then(data => {
      this.categories = data.items;
      // made it like this cauz to not show Resto & Bar category cauz zied said so :p
      for (let i = 1; i < this.categories.length; i++) {
        this.categories[i].type = 1;
        // this.allCategories.push(this.categories[i]);
      }
      this.businessService.getSubCategories().then(data => {
        this.subCategories = data.items;
        for (let i = 0; i < this.subCategories.length; i++) {
          this.subCategories[i].type = 2;
          this.allCategories.push(this.subCategories[i]);
        }
        this.inputText = this.currentInput;
        this.allCategories.push(this.match_keyword);
        //console.log(me.allKeywords);
      });
    });
    if (localStorage.getItem("categorie") != "") {
      this.titleService.setTitle("Les 10 meilleurs " + localStorage.getItem("categorie") + " sur le Grand Tunis | Vynd");
      this.metaDescription()
    }


  }

  putRegion(test) {
    this.currentRegion = test;
  }
  putInputText(test) {
    let self = this;
    setTimeout(function () {
      self.inputText = test;
    }, 1000);
    localStorage.setItem("categorie", test)
    if (localStorage.getItem("region") != "" && localStorage.getItem("categorie") != "") { this.titleService.setTitle("Les 10 meilleurs " + localStorage.getItem("categorie") + " au " + localStorage.getItem("region") + " | Vynd"); }
    else {
      this.titleService.setTitle("Les 10 meilleurs " + localStorage.getItem("categorie") + " sur le Grand Tunis | Vynd");
    }
    this.metaDescription()
  }

  onRegionSelected($event) {
    localStorage.setItem("region", $event.label)
    this.metaDescription()
    //console.log("on Select " +(localStorage.getItem("region")))
    let x = this.searchBody.tags.find(x => x.type == 3);
    if (x) {
      this.searchBody.tags.splice(this.searchBody.tags.indexOf(x), 1);
    }
    this.searchBody.tags.push($event);
    this.titleService.setTitle("Les 10 meilleurs " + this.inputText + " au " + $event.label + " | Vynd");

  }


  onRegionDeselected($event) {
    let index = (this.searchBody.tags.indexOf($event));
    if (index > -1) {
      this.searchBody.tags.splice(index, 1);
    }
    let x = this.searchBody.tags.find(x => x.type == 3);
    if (x) {
      this.searchBody.tags.splice(this.searchBody.tags.indexOf(x), 1);
    }
    localStorage.setItem("region", "");
    this.titleService.setTitle("Les 10 meilleurs " + localStorage.getItem("categorie") + " sur le Grand Tunis | Vynd");
    this.metaDescription()
  }

  goToCategory(category: Tag) {
    let x = this.searchBody.tags.find(x => x.type == 2);
    if (x) {
      this.searchBody.tags.splice(this.searchBody.tags.indexOf(x), 1);
    }
    // this.searchBody.tags=[];
    this.searchBody.tags.push(category);
    localStorage.setItem("search", JSON.stringify(this.searchBody));
    //this was wess works i didn't understand why he did it this way but mine works just fine
    /*if (this.router.url.substring(0,this.router.url.length)==="/searchresult"){
      this.searchService.setSearch();
      
    }
    else {
      this.router.navigate([this.NavigationString()]);
    }*/
    //zaid works
    this.searchService.setSearch();
    this.router.navigate([this.NavigationString()]);
  }
  goToBusiness(business: Business) {
    this.router.navigate(['v/' + business.id + '/' + business.metaIdentifier]);
    this.showSearchResponsive = false;
  }
  autoComplete(term: string) {
    this.showCategories = this.allCategories.filter(function (category) {
      return (category.label.toUpperCase().indexOf(term.toUpperCase()) != -1);
    });
    if (term != "") {
      if (term.startsWith("#")) {
        this.businesses = [];
      }
      else {
        this.busy_search = this.businessService.aucomplete(term).then(data => {
          this.businesses = data.items;
          this.show_autocomplete = true;
        });
      }
    }
    else {
      this.businesses = [];
      this.show_autocomplete = false;
    }
  }

  public inputOnFocus($event) {
    if ($event.target.value == "") {
      this.showCategories = [];
      for (let i = 0; i < this.allCategories.length; i++) {
        this.showCategories.push(this.allCategories[i]);
      }
    }
    this.show_autocomplete_box = true;
  }
  public inputOnBlur($event) {
    let self = this;
    setTimeout(function () {
      self.show_autocomplete_box = false;

    }, 500);

    //this.titleService.setTitle("Les 10 meilleurs " + this.inputText + " à " + $event.label + " | vynd");

  }

  metaDescription() {

    this.businessService.getmetaDataDescription(JSON.parse(localStorage.getItem("search"))).then(data => {
      this.nbAvis = data.nbAvis
      this.nbBusinesses = data.nbBusinesses
      if (localStorage.getItem("region") != "" && localStorage.getItem("categorie") != "") { this.metaService.updateTag({ property: 'og:description', content: ("meilleurs " + localStorage.getItem("categorie") + " à " + localStorage.getItem("region") + " sur Vynd : lisez " + this.nbAvis + " avis sur " + this.nbBusinesses + " " + localStorage.getItem("categorie") + " à " + localStorage.getItem("region") + ", recherchez par spécialité, budget, région, temps d’ouverture, popularité.") }, `property="og:description"`); }
      else {
        if (localStorage.getItem("categorie") != "" && localStorage.getItem("region") == "") { this.metaService.updateTag({ property: 'og:description', content: (" meilleurs " + localStorage.getItem("categorie") + " sur le Grand Tunis : lisez " + this.nbAvis + " avis sur " + this.nbBusinesses + "  " + localStorage.getItem("categorie") + " sur le Grand Tunis, recherchez par spécialité, budget, région, temps d’ouverture, popularité.") }, `property="og:description"`); }
      }
    })




  }

  search() {

    if (this.inputText != "") {
      let x = this.searchBody.tags.find(x => (x.label == this.inputText) && (x.type != 3));
      if (!x) {
        this.searchBody.tags.push({
          "id": 0,
          "label": this.inputText,
          "type": 0
        })
      }
      //   console.log("selectedRegion"+this.selectedRegion);
      this.titleService.setTitle("Les 10 meilleurs " + localStorage.getItem("categorie") + " au " + localStorage.getItem("region") + " | Vynd");
    }
    localStorage.setItem("search", JSON.stringify(this.searchBody));
    /*
    if (this.router.url.substring(0,this.router.url.length)==="/searchresult"){
      this.searchService.setSearch();
    }
    else {
      this.router.navigate([this.NavigationString()]);
    }*/
    this.searchService.setSearch();
    this.router.navigate([this.NavigationString()]);
  }

  open() {
    const modalRef = this.modalService.open(DownloadModalComponent);
    //  modalRef.componentInstance.name = 'World';
  }


  open_login() {
    const modalRef = this.dialog.open(LoginModalComponent, {
      //  panelClass: 'my-dialog'
    });
  }

  logout() {
    this.authService.logoutUser();
  }

  NavigationString() {
    var loc: string = ""
    var category = ""
    var query
    var search = JSON.parse(localStorage.getItem("search")).tags
    search.forEach(tag => {
      if (tag.type == 3) {
        loc = tag.label.replace(/\s/g, '')
      }
      if (tag.type == 2) {
        category = tag.label.replace(/\s/g, '')
      }
    });
    if (loc == "") {
      loc = "surLeGrandTunis"
    }
    if (category == "") {
      category = "toutLesCategories"
    }
    query = "searchresult/" + category + "/" + loc
    console.log("hedhi query" + query)
    console.log(localStorage.getItem("search"))
    return query

  }
}
