import { Component, OnInit } from '@angular/core';
import {ProfileUserModel} from "../../shared/profileUser.model";
import {BusinessService} from "../../core/business.service";
import {ActivatedRoute, Router} from "@angular/router";
import { AuthentificationService} from "../../core/auth.service";
import {LoginModalComponent} from "../../core/login-modal/login-modal.component";
//import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-profile-user-subscriptions',
  templateUrl: './profile-user-subscriptions.component.html',
  styleUrls: ['./profile-user-subscriptions.component.css']
})
export class ProfileUserSubscriptionsComponent implements OnInit {


  users:ProfileUserModel[]=[];
  constructor(private businessService: BusinessService, private route: ActivatedRoute, private router: Router, private authService: AuthentificationService,
     private modalService:MatDialog
    ) { }

  ngOnInit() {
    this.route.parent.params.subscribe(params => {
      this.businessService.getFollowings(params["id"]).then(data=> {
        this.users=data.items;
      })
    });
  }

  goToUser(id:number){
    if (this.authService.isUserLoggedIn()) {
      this.router.navigate(['/user/'+id])
    }
    else {
     const modalRef = this.modalService.open(LoginModalComponent);
    }
  }

}
