import { ActivatedRoute, Router } from "@angular/router";
import { Business, MenuItem, WorkingHour } from "../shared/business.model";
import { BusinessService } from "../core/business.service";
import { Utils } from "../shared/utils";
import { Img } from "../shared/img.model";
import { Comment } from "../shared/comment.model";
//import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { DownloadModalComponent } from "../core/download-modal/download-modal.component";
import { PhotoViewComponent } from "../core/photo-view/photo-view.component";
//import {DOCUMENT} from "@angular/common";
