  activeMe(index) {
    this.active = [0, 0, 0, 0]
    this.active[index] = 1




  }


  createRangeDesc(number) {
    number = 5 - Math.round(number);
    var items: number[] = [];
    for (var i = 1; i <= number; i++) {
      items.push(i);
    }
    return items;

  }
  createRange(number) {
    number = Math.round(number);
    var items: number[] = [];
    for (var i = 1; i <= number; i++) {
      items.push(i);
    }
    return items;
  }

  getBusiness() {
    this.businessService
  }

  openTimeToday(workingHours: WorkingHour[]) {
    if (Utils.check(workingHours)) {
      let date = new Date();
      let x = workingHours.find(x => x.dayOfTheWeek == date.getDay());
      if (x) {
        if (x.hours.length > 0) {
          return x.hours;
        }
        else return [];
      }
      return [];
    }
    else {
      return [];
    }

  }

  openTimeDay(workingHours: WorkingHour[], day: number) {
    if (Utils.check(workingHours)) {
      let x = workingHours.find(x => x.dayOfTheWeek == day);
      if (x) {
        if (x.hours.length > 0) {
          return x.hours;
        }
        else return [];
      }
      return [];
    }
    else {
      return [];
    }

  }

  clickMoreHours() {
    this.showMoreHours = !this.showMoreHours;
  }

  open() {
    const modalRef = this.modalService.open(DownloadModalComponent);
    //   modalRef.componentInstance.name = 'World';
  }

  open_photo(photoUrl: string) {
    const modalRef = this.modalService.open(PhotoViewComponent);
    modalRef.componentInstance.photo = photoUrl;
  }

  pushComment(comment: Comment) {
    this.comments.unshift(comment);
  }


  positions



