import { Component, OnInit } from '@angular/core';
import { Business } from "../../shared/business.model";
import { SearchParams } from "../../shared/searchParams.model";
//import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import { Router } from "@angular/router";
import { BusinessService } from "../business.service";
import { Subject } from "rxjs/Subject";
import { AuthentificationService } from "../auth.service";
import { CommentToPostModel } from "../../shared/commentToPost.model";
import { LoggedUserModel } from "../../shared/loggedUser.model";
import { ErreurModalComponent } from "../erreur-modal/erreur-modal.component";
import { SuccessModalComponent } from "../success-modal/success-modal.component";
import { Utils } from "../../shared/utils";
import { LoginModalComponent } from "../login-modal/login-modal.component";
import { LoginCommentModalComponent } from "../login-comment-modal/login-comment-modal.component";
import { SearchService } from "../search.service";
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { LoadComponent } from "./../../load/load.component";

declare var $: any;
@Component({
  selector: 'app-post-review',
  templateUrl: './post-review.component.html',
  styleUrls: ['./post-review.component.css']
})
export class PostReviewComponent implements OnInit {

  searchBody_comment_businesses: SearchParams = {
    "tags": [],
    "latitude": "",
    "longitude": "",
    "distance": 0,
    "open": 0,
    "coupon": 0,
    "popularity": 0,
    "note": 0,
    "budget": 0,
    "searchId": ""
  };

  inputTextComment = "";

  stars: Array<String> = [];
  emptyStar = "./../../assets/img/empty.svg";
  fullStar = "./../../assets/img/full.svg";
  choosen = false;
  nbStar = 0
  hexagones = [
    {
      "label": "Accueil et service :",
      "margin": 25,
      "rate": 0,
      "choosen": false,
      "stars": []
    },

    {
      "label": "Cuisine :",
      "margin": 79,
      "rate": 0,
      "choosen": false,
      "stars": []
    },
    {
      "label": "Ambiance :",
      "margin": 64,
      "rate": 0,
      "choosen": false,
      "stars": []
    },
    {
      "label": "Rapport qualité/prix :",
      "margin": 10,
      "rate": 0,
      "choosen": false,
      "stars": []
    },
    {
      "label": "Hygiène et propreté :",
      "margin": 11,
      "rate": 0,
      "choosen": false,
      "stars": []
    }
  ];

  content = "";

  userLoggedin: LoggedUserModel = {};
  show_autocomplete_comment = false;
  show_autocomplete_comment_box = false;
  businessesCommentFirst: Business[] = [];
  businessesComment: Business[] = [];
  selectedBusinessComment: Business = {};
  isSelectedBusinessComment = false;
  searchTermCommentStream = new Subject<string>();
  busy_comment_businesses: Promise<any>;
  currentRate = 0;
  nbrRate = 0;
  comment: CommentToPostModel = {};
  busy: Promise<any>;
  loading = LoadComponent


  constructor(private searchService: SearchService, private businessService: BusinessService, private router: Router,
    private modalService: MatDialog,
    private authService: AuthentificationService) {
    this.searchService.notifyEmptyComment.subscribe(test => this.emptyComment());
    this.searchTermCommentStream
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe((term) => this.autocompleteCommentBusinesses(term));
  }

  ngOnInit() {
    this.busy_comment_businesses = this.businessService.searchFull(this.searchBody_comment_businesses, 0, 10).then(data => {
      this.businessesCommentFirst = data.items;
      for (let i = 0; i < this.businessesCommentFirst.length; i++) {
        this.businessesComment.push(this.businessesCommentFirst[i]);
      }
    });

    for (let i = 0; i < this.hexagones.length; i++) {
      for (let j = 0; j < 5; j++) {
        this.hexagones[i].stars.push(this.emptyStar)
      }
    }
  }


  selectBusinessComment(business: Business) {
    this.selectedBusinessComment = business;
    this.isSelectedBusinessComment = true;
  }

  deselectBusinessComment() {
    this.selectedBusinessComment = {};
    this.isSelectedBusinessComment = false;
  }

  rateSelected() {
    let h = 0;
    let k = 0;
    for (let i = 0; i < this.hexagones.length; i++) {
      if (this.hexagones[i].rate > 0) {
        h += this.hexagones[i].rate;
        k++;
      }
    }
    if (this.nbrRate < k) {
      this.nbrRate++;
    }
    this.currentRate = Math.round(h / k);

  }

  public inputCommentOnFocus($event) {
    this.show_autocomplete_comment_box = true;
  }

  public inputCommentOnBlur() {
    let self = this;
    setTimeout(function () {
      self.show_autocomplete_comment_box = false;

    }, 500);

  }

  autocompleteCommentBusinesses(term: string) {
    if (term != "") {
      this.busy_comment_businesses = this.businessService.aucomplete(term).then(data => {
        this.businessesComment = data.items;
      });
    }

    else {
      this.businessesComment = [];
      for (let i = 0; i < this.businessesCommentFirst.length; i++) {
        this.businessesComment.push(this.businessesCommentFirst[i]);
      }
    }

  }

  postComment() {
    this.rateSelected()
    this.comment.businessId = this.selectedBusinessComment.id;
    this.comment.rate = this.currentRate;
    this.comment.service = this.hexagones[0].rate;
    this.comment.cookery = this.hexagones[1].rate;
    this.comment.ambience = this.hexagones[2].rate;
    this.comment.quality = this.hexagones[3].rate;
    this.comment.hygiene = this.hexagones[4].rate;
    this.comment.content = this.content;
    this.comment.budget = 0;

    if (this.checkComment(this.comment)) {

      this.userLoggedin = this.authService.getCurrentUser();
      if (this.userLoggedin) {

        this.busy = this.businessService.postComment(this.comment, this.userLoggedin.token).then(data => {
          if (Utils.check(data.item2)) {
            const succees = this.modalService.open(SuccessModalComponent);
            succees.componentInstance.texte = 'Merci ' + [this.userLoggedin.firstName] + ' ! Cet avis va aider nos amis Vynders à bien choisir où sortir.';
            succees.componentInstance.photo = '../../../assets/img/9erd_love.svg';
            this.emptyComment();
          }
          else {
            const erreur = this.modalService.open(ErreurModalComponent);
          }
        });
      }
      else {
        const login = this.modalService.open(LoginCommentModalComponent);
        login.componentInstance.comment = this.comment;
      }
    }

  }

  checkComment(comment: CommentToPostModel): boolean {
    if (comment.businessId > 0 && comment.rate > 0 && comment.service > 0 && comment.ambience > 0 && comment.quality > 0 && comment.hygiene > 0) {
      return true;
    }
    else {
      const erreur = this.modalService.open(ErreurModalComponent);
      if (Utils.check(localStorage.getItem('current_vyndee'))) {
        erreur.componentInstance.text = "Tu dois remplir tous les champs !";
      }
      else {
        erreur.componentInstance.text = "Vous devez remplir tous les champs !";
      }

      return false;
    }
  }


  HoverMe(indexHexagone, rate) {

    for (let k = 0; k < this.hexagones.length; k++) {
      if (this.hexagones[k].choosen == false) {
        for (let a = 0; a < 5; a++) {
          this.hexagones[k].stars[a] = this.emptyStar
        }
        this.hexagones[k].rate = 0
      } else {
        for (let a = 0; a < this.hexagones[k].rate; a++) {
          this.hexagones[k].stars[a] = this.fullStar
        }
        for (let j = 4; j > this.hexagones[k].rate; j--) {
          this.hexagones[k].stars[j] = this.emptyStar;
        }
      }
    }

    console.log(indexHexagone + '--' + rate)
    for (let j = 0; j <= rate; j++) {
      this.hexagones[indexHexagone].stars[j] = this.fullStar;
    }
    for (let j = 4; j > rate; j--) {
      this.hexagones[indexHexagone].stars[j] = this.emptyStar;
    }
    //  console.log(this.hexagones[indexHexagone].stars )

  }

  toEmptyAll() {
    for (let k = 0; k < this.hexagones.length; k++) {
      if (this.hexagones[k].choosen == true) {
        
        this.HoverMe(k, this.hexagones[k].rate - 1);
      } else {
        this.HoverMe(k, -1);
      }
    }


  }




  ActiveMe(indexHexagone, rate) {
    this.hexagones[indexHexagone].choosen = true;
    this.HoverMe(indexHexagone, rate);
    this.hexagones[indexHexagone].rate = rate + 1;
    this.hexagones.filter((tab) => {
      console.log(tab.choosen)
    })

  }

  emptyCommentProfileBusiness() {
    this.selectedBusinessComment = {};
    this.isSelectedBusinessComment = false;
    for (let i = 0; i < this.hexagones.length; i++) {
      this.hexagones[i].rate = 0;
    }
    this.content = "";
    this.currentRate = 0;
    this.nbrRate = 0;
  }
  emptyComment() {
    this.selectedBusinessComment = {};
    this.isSelectedBusinessComment = false;
    for (let i = 0; i < this.hexagones.length; i++) {
      this.hexagones[i].rate = 0;
      this.hexagones[i].choosen=false;
    }
    this.toEmptyAll();
    this.content = "";
    this.currentRate = 0;
    this.nbrRate = 0;
  }

}
